.social {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  margin-left: 73px;
  margin-right: 77px;
}

.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: block;
  fill: $grey-extra;
}

.social__link:hover {
  fill: $green;
}

.social__link:active {
  fill: $green;
  opacity: 0.3;
}

.social__link--facebook {
  stroke: $grey-extra;
  fill: none;
}

.social__link--facebook:hover {
  fill: none;
  stroke: $green;
}

.social__link--facebook:active {
  fill: none;
  stroke: $green;
}

@media (min-width: $tablet-width) {
  .social {
    margin: 0;
    justify-content: flex-start;
    // padding-left: 50px;
    // padding-right: 59px;
  }

  .social__item {
    margin-right: 21px;
    flex-shrink: 0;
  }

  .social__item--vkontakte {
    margin-right: 26px;
    margin-left: 48px;
  }

  .social__svg--vkontakte {
    width: 30px;
    height: 22px;
  }

  .social__svg--instagram {
    width: 23px;
    height: 23px;
  }

  .social__svg--facebook {
    width: 28px;
    height: 33px;
  }
}

@media (min-width: $desktop-width) {
  .social {
    padding-left: 135px;
    padding-right: 135px;
  }

  .social__item--vkontakte {
    margin-left: 0;
    margin-right: 25px;
  }
}
